div {
    height: 100%;
    overflow: hidden;
}

#topBar {
    height: 50px;
    background: #61dafb;
}

#grid {
    /*height: calc(100% - 20px);*/
    width: 100%;
    height: 0;
    padding-top: 100%;
    position: relative;
    background: #111111;
    /*background: #1e1e1e;*/
    overflow: hidden;
}

table {
    width: 100%;
    height: 100%;
    table-layout: fixed;
    border-collapse: collapse;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    /*display: block;*/
}

td {
    position:relative;
    /*border: 1px solid #ffffff;*/
    text-align: center;
    border-radius: 10px;
}
